* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    font-family: 'Montserrat', sans-serif;
}

/*
    Header
*/

header {
    background: #0353a4;
    box-shadow: 0px 2px 6px 1px gray;
}

header nav {
    margin: 0 auto;
    max-width: 120rem;
}

header nav ul {
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    margin-right: 1rem;
    padding: 2em 0;
}

header nav ul li {
    padding: .5em;
    margin: 0 1em;
}

header nav ul li a {
    text-decoration: none;
    font-size: 1.7rem;
    text-transform: uppercase;
    color: white;
    font-weight: 700;
}

header nav ul li a:hover,
header nav ul li a:focus, 
header nav ul li a.active {
    color: #b9d6f2;
}

/*
    Map
*/
#center {
    display: flex;
    max-width: 120rem;
    margin: 5rem auto;
}

#map {
    max-width: 50rem;
}

#hero-title {
    display: flex;
    flex: 1 0 auto;
    margin-left: 10rem;
    align-items: center;
    justify-content: center;
    margin-bottom: 20rem;
    flex-direction: column;
}

#hero-title a {
    background: #0353a4;
    color: #fafafa;
    text-decoration: none;
    padding: .7em;
    font-size: 2.5rem;
    margin-top: .7em;
    text-transform: uppercase;
    box-shadow: 4px 4px 16px -2px gray;
}

#hero-title h1 {
    font-size: 8.2rem;
    color: #0353a4;
    letter-spacing: .7rem;
    font-family: 'Permanent Marker', sans-serif;
}

#hero-title p {
    font-size: 1.7rem;
    max-width: 50rem;
    text-align: center;
    margin: .3em 0;
    line-height: 1.2em;
    margin-bottom: 1em;
}

/*
    SVG image
*/

.circle-star {
    opacity: 0;
    transition: opacity ease-in 1s;
}

.circle-star.active {
    opacity: 1;
}